<template>
  <el-form
    class="edit-file-wrap file-form"
    ref="form"
    :model="form"
    :rules="formRule"
    label-width="95px"
    label-position="left"
    size="mini"
    status-icon
  >
    <el-form-item label="标题名称：" prop="bpName">
      <el-input
        v-model="form.bpName"
        class="name-input"
        maxlength="60"
        clearable
        show-word-limit
        placeholder="请输入名称"
      ></el-input>
    </el-form-item>
    <el-form-item label="位置：">
      <el-tooltip
        class="item"
        effect="dark"
        content="[ 0 - 1080 ]"
        placement="right"
      >
        <el-input-number
          :min="0"
          :max="1080"
          :precision="0"
          v-model="form.positioini"
        ></el-input-number>
      </el-tooltip>
    </el-form-item>
    <el-form-item label="速度">
      <el-tooltip
        class="item"
        effect="dark"
        content="[ 0 - 10 ]"
        placement="right"
      >
        <el-input-number
          :min="0"
          :max="10"
          :precision="0"
          v-model="form.speed"
        ></el-input-number>
      </el-tooltip>
    </el-form-item>
    <el-form-item label="字体大小">
      <el-tooltip
        class="item"
        effect="dark"
        content="[ 8 - 72 ]"
        placement="right"
      >
        <el-input-number
          :min="8"
          :max="72"
          :precision="0"
          v-model="form.fontSize"
        ></el-input-number>
      </el-tooltip>
    </el-form-item>
    <el-form-item label="背景颜色">
      <el-color-picker show-alpha v-model="form.background"></el-color-picker>
    </el-form-item>
    <el-form-item label="字体颜色">
      <el-color-picker show-alpha v-model="form.color"></el-color-picker>
    </el-form-item>
    <el-form-item label="文本内容：" prop="text">
      <el-input
        v-model="form.text"
        class="name-input"
        clearable
        show-word-limit
        placeholder="请输入文本"
      ></el-input>
    </el-form-item>
    <el-form-item label="播放时长：" class="mb20" prop="duration">
      <el-time-picker
        class="text-duration"
        v-model="form.duration"
        value-format="HH:mm:ss"
        :picker-options="{ selectableRange: '00:00:00 - 23:00:00' }"
        placeholder="请选择播放时长"
      ></el-time-picker>
    </el-form-item>
    <el-form-item label="循环模式：" class="mb20">
      <el-radio-group v-model="form.loopMode" class="timing-radio">
        <el-radio :label="0">
          <span class="text">循环次数</span>
          <el-input-number
            :min="0"
            :max="1000"
            :precision="0"
            v-model="form.loopCount"
          ></el-input-number>
          <em class="t">次</em>
        </el-radio>
        <br />
        <el-radio :label="2">
          <span class="text">循环播放到停止</span>
        </el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="循环间隔：">
      <el-input-number
        :min="1"
        :max="100"
        :precision="0"
        v-model="form.loopInterval"
      ></el-input-number>
      <em class="t">秒</em>
    </el-form-item>
  </el-form>
</template>

<script>
import api from "@/scripts/api";
import { MakeDurationToSeconds, FormatDuration } from "@/scripts/utils";
export default {
  data() {
    const ValidateDuration = (rule, duration, callback) => {
      if (duration && duration !== "00:00:00") {
        callback();
      } else {
        callback(new Error("请设置播放时长"));
      }
    };
    return {
      isEdit: true,
      speedOptions: [
        {
          label: "标准",
          value: 0,
        },
        {
          label: "快速",
          value: 1,
        },
        {
          label: "慢速",
          value: 2,
        },
      ],
      posOptions: [
        {
          label: "屏幕上方",
          value: 0,
        },
        {
          label: "屏幕中间",
          value: 1,
        },
        {
          label: "屏幕下方",
          value: 2,
        },
      ],
      form: {
        positioini: 0,
        speed: 5,
        fontSize: 36,
        background: "rgba(255, 255, 255, 0)",
        color: "rgba(0, 0, 0, 1)",
        text: "",
        pos: 0,
        duration: "00:00:00",
        bpName: "",
        loopInterval: 1,
        loopCount: 0,
        loopMode: 0,
      },
      formRule: {
        bpName: [
          {
            required: true,
            message: "请输入标题名称",
            trigger: "blur",
          },
        ],
        text: [
          {
            required: true,
            message: "请输入文本内容",
            trigger: "blur",
          },
        ],
        duration: [
          {
            required: true,
            trigger: "blur",
            validator: ValidateDuration,
          },
        ],
      },
    };
  },
  mounted() {
    var bplid = this.$route.params.bplid;
    if (bplid) {
      this.LoadData();
    }
  },
  methods: {
    async LoadData() {
      const res = await api.BP.GetBroadcastPlan(this.$route.params.bplid);
      if (res && res.Result) {
        this.form.bpName = res.Data.BPLName;
        this.form.duration = `${FormatDuration(res.Data.BPLDuration)}`;
        this.form.loopMode = res.Data.BPLLoopMode;
        this.form.loopInterval = res.Data.BPLLoopInterval;
        this.form.loopCount = res.Data.BPLLoopCount;
        if (res.Data.BPLTextParam) {
          var txtParam = JSON.parse(res.Data.BPLTextParam);
          this.form.positioini = txtParam.Positioini;
          this.form.speed = txtParam.Speed;
          this.form.text = txtParam.Text;
          this.form.fontSize = txtParam.FontSize;
          this.form.background = this.ReverseFormatColor(txtParam.Background);
          this.form.color = this.ReverseFormatColor(txtParam.Color);
        }
      }
    },
    FormatColor(color) {
      let c = color.substring(5, color.length - 1);
      c = c.replace(/\s+/g, "");
      const arr = c.split(",");
      return arr[3] + "," + arr[0] + "," + arr[1] + "," + arr[2];
    },
    ReverseFormatColor(color) {
      var colorArr = color.split(",");
      return (
        "rgba(" +
        colorArr[1] +
        "," +
        colorArr[2] +
        "," +
        colorArr[3] +
        "," +
        colorArr[0] +
        ")"
      );
    },
    async Confirm() {
      this.$refs.form.validate(async (valid) => {
        if (valid) {
          var txtParam = {
            Positioini: this.form.positioini,
            Speed: this.form.speed,
            Text: this.form.text,
            FontSize: this.form.fontSize,
            LoopMode: this.form.loopMode === 2 ? -1 : this.form.loopCount,
            Background: this.FormatColor(this.form.background),
            Color: this.FormatColor(this.form.color),
          };
          var time = `${MakeDurationToSeconds(this.form.duration)}`;
          const res = await api.BP.AddPlanItem(
            this.$route.params.bplid,
            this.$route.params.bpid,
            this.form.bpName,
            3,
            null,
            this.form.loopMode,
            this.form.loopInterval,
            this.form.loopCount,
            time,
            JSON.stringify(txtParam)
          );
          if (res && res.Result) {
            this.$emit("ConfirmCompleted");
          } else {
            var msg = "";
            if (res.Info) {
              res.Info.forEach((element) => {
                msg += element + "\r\n";
              });
            } else {
              msg = "添加失败";
            }
            this.$message({
              message: msg,
              type: "warning",
              duration: 1000,
            });
          }
        }
      });
    },
  },
};
</script>

<style lang="less">
.file-form {
  .quill-editor .ql-editor {
    min-height: 150px;
  }
  .text-duration {
    width: 540px !important;
  }
}
</style>