<template>
  <el-cascader
    v-model="Value"
    :placeholder="placeholder"
    :options="options"
    :props="oprops"
    :show-all-levels="false"
    clearable
    @change="ChangeHandle"
  >
    <template slot-scope="{ node, data }">
      <span>{{ data.AreasShortName }}</span>
    </template>
  </el-cascader>
</template>

<script>
import api from "@/scripts/api";

export default {
  props: {
    value: {
      type: String,
      default: ""
    },
    placeholder: {
      type: String,
      default: "请选择"
    }
  },
  watch: {
    value(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.Value = newVal;
        this.$nextTick(() => {
          this.LoadTreeData(0, null, this);
        });
      }
    }
  },
  data() {
    return {
      Value: this.value,
      options: [],
      oprops: {
        emitPath: false,
        value: "AreasDigitalCode",
        id: "AreasID",
        label: "AreasShortName",
        lazy: true,
        children: "options",
        expandTrigger: "hover",
        checkStrictly: true,
        async lazyLoad(node, resolve) {
          if (node.level > 0) {
            const res = await api.Areas.GetSubDatas(node.data.AreasID);
            if (res) {
              resolve(res.Datas);
            }
          }
        }
      }
    };
  },
  mounted() {
    this.LoadTreeData(0, null, this);
  },
  methods: {
    ChangeHandle(val) {
      this.$emit("update:value", this.Value);
    },
    /**
     * 加载基础数据
     */
    async LoadTreeData(level, pid, parent) {
      const res = await api.Areas.GetSubDatas(pid);

      if (res) {
        res.Datas.forEach(async element => {
          if (element.AreasDigitalCode !== this.value) {
            if (
              this.value.slice(0, 2 * (level + 1)) ===
              element.AreasDigitalCode.slice(0, 2 * (level + 1))
            ) {
              if (!parent.options || parent.options.length <= 0) {
                await this.LoadTreeData(level + 1, element.AreasID, element);
              } else {
                const find = parent.options.find(
                  f => f.AreasID === element.AreasID
                );
                await this.LoadTreeData(level + 1, element.AreasID, find);
              }
            }
          }
        });

        if (!parent.options || parent.options.length <= 0) {
          this.$set(parent, "options", res.Datas);
        }
      }
    }
  }
};
</script>

