<template>
  <div id="week-course">
    <Week :value.sync="curdate" @ValueChanged="ChangeDate"></Week>
    <Table
      ModuleCode="EVALManage"
      ModuleName="本周评课"
      :Loading="Loading"
      :ShowExport="false"
      :ShowCreate="false"
      KeyFileld="EVALPID"
      :Columns="Columns"
      :SortField.sync="SortField"
      :SortDir.sync="SortDir"
      :Datas.sync="Datas"
      :PageIndex.sync="PageIndex"
      :PageSize.sync="PageSize"
      :TotalCounts="TotalCounts"
      :SearchKey.sync="SKey"
      :SearchValue.sync="Key"
      :UnNeedEstateBtn="true"
      @LoadData="LoadData"
      :UnNeedDelBtn="true"
      :ShowSelectColumn="false"
      @ViewRowHandle="View"
    >
    </Table>
  </div>
</template>
<script>
import api from "@/scripts/api";
import { FormatDate } from "@/scripts/utils";
export default {
  props: ["GetNum"],
  components: {
    Table: () => import("@/components/Table"),
    Week: () => import("@/components/WeekCalendar")
  },
  data() {
    return {
      Loading: false,
      Columns: [
        {
          Code: "EVALPName",
          Header: "计划名称"
        },
        {
          Code: "Teacher",
          Header: "教师名称"
        },
        {
          Code: "CourseName",
          Header: "课程名称"
        },
        {
          Code: "UserName",
          Header: "评价人"
        },
        {
          Code: "typeName",
          Header: "评价人角色"
        },
        {
          Code: "RFCreateDate",
          Header: "评价时间"
        },
        {
          Code: "score",
          Header: "分数"
        },
        {
          Code: "oprate",
          Header: "操作",
          Width: "120",
          NeedViewBtn: true,
          UnNeedEditBtn: true,
          UnNeedEstateBtn: true,
          UnNeedDelBtn: true,
          Slot: true,
          ShowBaseActionBtn: true
        }
      ],
      Datas: [],
      PageIndex: 1,
      PageSize: 20,
      TotalCounts: 0,
      Key: "",
      SKey: "",
      SortField: "",
      SortDir: 0,
      Estate: "",
      activeI: 0,
      curdate: FormatDate(new Date(), "yyyy-MM-dd")
    };
  },
  mounted() {},
  watch: {
    GetNum(val) {
      if (val > 0) {
        this.LoadData(this.curdate);
      }
    }
  },
  methods: {
    View(r) {
      this.$emit("WeekView", r);
    },
    ChangeDate(s) {
      this.PageIndex = 1;
      this.PageSize = 20;
      this.LoadData();
    },
    async LoadData(date) {
      this.Datas = [];
      this.Loading = true;
      const res = await api.EVALPlan.GetWeekEvaluationList(
        this.curdate,
        this.PageIndex,
        this.PageSize,
        this.SortField,
        this.SortDir
      );
      if (res && res.Datas && res.Datas.length > 0) {
        res.Datas.forEach(ele => {
          ele.time = FormatDate(ele.RFCreateDate, "yyyy-MM-dd");
          ele.usertype === "1"
            ? (ele.typeName = "老师")
            : (ele.typeName = "学生");
        });
        this.Datas = res.Datas;
        this.TotalCounts = res.rowsCount;
      } else {
        this.Datas = [];
        this.TotalCounts = 0;
      }
      this.Loading = false;
    }
  }
};
</script>
<style lang="less">
#week-course {
  .tbl .handle_bar {
    display: none;
  }
  .el-table .el-table__header thead tr th {
    padding: 6px 5px;
  }
  .el-badge__content {
    display: none;
  }
}
</style>
