<template>
  <div class="actionSet">
    <p>
      动作集
      <el-button-group>
        <el-button
          class="add"
          type="primary"
          icon="el-icon-circle-plus-outline"
          size="mini"
          v-if="isAdd"
          @click="add"
        >动作</el-button>
        <el-button class="update" type="primary" icon="el-icon-edit" size="mini" v-if="!isAdd" @click="saveAction">保存</el-button>
        <el-button class="delete" type="danger" icon="el-icon-close" size="mini" v-if="!isAdd" @click="add">取消</el-button>
      </el-button-group>
    </p>
    <el-form v-if="!isAdd" ref="form" :model="form" :rules="rules" size="mini" label-width="90px">
      <el-row :gutter="5">
        <el-col :span="12">
          <el-form-item label="设备类型:" prop="devType">
            <el-select
              filterable
              :default-first-option="true"
              @change="changeOfType"
              v-model="form.devType"
              placeholder="请选择"
            >
              <el-option v-for="(item,index) in devTypeOpts" :key="index" :label="item.label" :value="item.value"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="12" v-if="form.devType != 0">
          <el-form-item label="设备:" prop="devList">
            <el-select
              filterable
              :default-first-option="true"
              @change="selectDevice"
              v-model="form.devList"
              placeholder="请选择"
            >
              <el-option v-for="(item,index) in devListOpts" :key="index" :label="item.label" :value="item"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="12" v-if="form.devType === 0">
          <el-form-item label="情景:" prop="sceneList">
            <el-select filterable :default-first-option="true" v-model="form.sceneList" placeholder="请选择">
              <el-option v-for="(item,index) in sceneListOpts" :key="index" :label="item.label" :value="item"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="12" v-if="form.devType == 1 && form.devType != 0">
          <el-form-item label="按钮:" prop="btnList">
            <el-select filterable :default-first-option="true" v-model="form.btnList" placeholder="请选择">
              <el-option v-for="(item,index) in btnListOpts" :key="index" :label="item.label" :value="item"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="12" v-if="form.devType == 3 && form.devType != 0">
          <el-form-item label="虚拟面板:" prop="panelList">
            <el-select
              filterable
              :default-first-option="true"
              @change="selectPanel"
              v-model="form.panelList"
              placeholder="请选择"
            >
              <el-option v-for="(item,index) in panelListOpts" :key="index" :label="item.label" :value="item"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="12" v-if="form.devType != 2 && form.devType != 0">
          <el-form-item label="动作:" prop="actionList">
            <el-select filterable :default-first-option="true" v-model="form.actionList" placeholder="请选择">
              <el-option v-for="(item,index) in actionListOpts" :key="index" :label="item.label" :value="item"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="12" v-if="form.devType == 2 && form.devType != 0">
          <el-form-item label="白光:" prop="white">
            <el-input v-model.number="form.white" :min="0" :max="100">
              <template slot="append">%</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12" v-if="form.devType == 2 && form.devType != 0">
          <el-form-item label="黄光:" prop="yellow">
            <el-input v-model.number="form.yellow" :min="0" :max="100">
              <template slot="append">%</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12" v-if="form.devType == 3 && isCustom">
          <el-form-item label="模式:" prop="patternList">
            <el-select filterable :default-first-option="true" v-model="form.patternList" placeholder="请选择">
              <el-option v-for="(item,index) in patternListOpts" :key="index" :label="item.label" :value="item.value"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="12" v-if="form.devType == 3 && isCustom">
          <el-form-item label="温度:" prop="temperature">
            <el-select filterable :default-first-option="true" v-model.number="form.temperature" placeholder="请选择">
              <el-option
                v-for="(item,index) in temperatureListOpts"
                :key="index"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="12" v-if="form.devType != 0">
          <el-form-item label="延时时间:" prop="delayed">
            <el-input v-model.number="form.delayed" :min="0" :max="30">
              <template slot="append">秒</template>
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div class="set" v-if="isAdd">
      <div class="action-item" v-for="(item,index) in actions" :key="index">
        <span v-if="item.type == 0">情景模式</span>
        <span v-if="item.type == 41 || item.type == 42 ||item.type == 43">面板设备</span>
        <span v-if="item.type == 44">调光器</span>
        <span v-if="item.type == 45">红外转发器</span>
        <span v-if="item.type == 46">风扇调速器</span>
        <span v-if="item.type == 47">窗帘控制器</span>
        <span v-if="item.devName">{{item.devName}} -</span>
        <span v-if="item.sceneName">{{item.sceneName}}</span>
        <span v-if="item.eqName">{{item.eqName}} -</span>
        <span v-if="item.panelName">{{item.panelName}} -</span>
        <span v-if="item.action === 0 && item.type < 47 ">关</span>
        <span v-if="item.action === 1 && item.type < 46 ">开</span>
        <span v-if="item.action > 0 && item.type == 46 ">{{item.action}} 档</span>
        <span v-if="item.action === 0 && item.type == 47 ">拉开</span>
        <span v-if="item.action === 1 && item.type == 47 ">关闭</span>
        <span v-if="item.action === 2 && item.type == 47 ">停止</span>
        <span v-if="item.w">白光度：{{item.w}}</span>
        <span v-if="item.y">黄光度：{{item.y}}</span>
        <span v-if="item.b6 === 0 && item.action != 0">- 自动</span>
        <span v-if="item.b6 === 1 && item.action != 0">- 制冷</span>
        <span v-if="item.b6 === 2 && item.action != 0">- 抽湿</span>
        <span v-if="item.b6 === 3 && item.action != 0">- 送风</span>
        <span v-if="item.b6 === 4 && item.action != 0">- 制热</span>
        <span v-if="item.b0 && item.b6 != 0 && item.action != 0">- {{item.b0}}度</span>
        <span v-if="item.actionName">{{item.actionName}}</span>
        <span v-if="item.time && item.time != 0">- 延时：{{item.time / 1000}} 秒</span>
        <span>
          <i class="el-icon-top" @click="moveupward(item,index)"></i>
          <i class="el-icon-bottom" @click="movedown(item,index)"></i>
          <i class="el-icon-delete" @click="removeaction(item,index)"></i>
        </span>
      </div>
      <span v-if="isAdd && actions.length === 0">无动作集</span>
    </div>
  </div>
</template>

<script>
import api from "@/scripts/api";
export default {
  props: {
    //  教室ID
    clsRoomID: {
      type: String,
      default: ""
    }
  },
  watch: {
    clsRoomID (newVal, oldVal) {
      if (newVal) {
        this.roomID = newVal;
      }
    }
  },
  data () {
    return {
      form: {
        devType: "",
        devList: "",
        sceneList: "",
        delayed: 0,
        btnList: "",
        panelList: "",
        actionList: "",
        temperature: 25,
        patternList: 1,
        white: 50,
        yellow: 50
      },
      rules: {
        devType: [
          {
            required: true,
            message: "请选择设备类型",
            trigger: "blur"
          }
        ],
        devList: [
          {
            required: true,
            message: "请选择设备",
            trigger: "blur"
          }
        ],
        sceneList: [
          {
            required: true,
            message: "请选择情景",
            trigger: "blur"
          }
        ],
        delayed: [
          {
            required: true,
            message: "请输入延时时间",
            trigger: "blur"
          },
          { type: "number", message: "必须为数字值" }
        ],
        btnList: [
          {
            required: true,
            message: "请选择按钮",
            trigger: "blur"
          }
        ],
        panelList: [
          {
            required: true,
            message: "请选择虚拟面板",
            trigger: "blur"
          }
        ],
        actionList: [
          {
            required: true,
            message: "请选择动作",
            trigger: "blur"
          }
        ],
        temperature: [
          {
            required: true,
            message: "请选择温度",
            trigger: "blur"
          },
          { type: "number", message: "必须为数字值" }
        ],
        patternList: [
          {
            required: true,
            message: "请选择模式",
            trigger: "blur"
          }
        ],
        white: [
          {
            required: true,
            message: "请输入白光度",
            trigger: "blur"
          },
          { type: "number", message: "必须为数字值" }
        ],
        yellow: [
          {
            required: true,
            message: "请输入黄光度",
            trigger: "blur"
          },
          { type: "number", message: "必须为数字值" }
        ]
      },
      isCustom: false,
      isAdd: true,
      devTypeOpts: [
        { label: "面板", value: 1 },
        { label: "调光器", value: 2 },
        { label: "红外转发器", value: 3 },
        { label: "风扇调速器", value: 4 },
        { label: "窗帘控制器", value: 5 }
      ],
      devListOpts: [],
      sceneListOpts: [],
      btnListOpts: [],
      panelListOpts: [],
      actionListOpts: [],
      temperatureListOpts: [
        { label: "19度", value: 19 },
        { label: "20度", value: 20 },
        { label: "21度", value: 21 },
        { label: "22度", value: 22 },
        { label: "23度", value: 23 },
        { label: "24度", value: 24 },
        { label: "25度", value: 25 },
        { label: "26度", value: 26 },
        { label: "27度", value: 27 },
        { label: "28度", value: 28 },
        { label: "29度", value: 29 },
        { label: "30度", value: 30 }
      ],
      patternListOpts: [
        { label: "制冷", value: 1 },
        { label: "抽湿", value: 2 },
        { label: "送风", value: 3 },
        { label: "制热", value: 4 }
      ],
      roomID: "",
      actions: []
    };
  },
  mounted () { },
  methods: {
    add () {
      this.isAdd = !this.isAdd;
    },
    // 动作上移
    moveupward (item, index) {
      if (index !== 0) {
        this.actions[index] = this.actions.splice(
          index - 1,
          1,
          this.actions[index]
        )[0];
      } else {
        this.actions.push(this.actions.shift());
      }
    },
    // 动作下移
    movedown (item, index) {
      if (index !== this.actions.length - 1) {
        this.actions[index] = this.actions.splice(
          index + 1,
          1,
          this.actions[index]
        )[0];
      } else {
        this.actions.unshift(this.actions.splice(index, 1)[0]);
      }
    },
    // 移除单个动作
    removeaction (item, index) {
      this.actions.splice(index, 1);
    },
    initialform () {
      this.form.devList = "";
      this.form.btnList = "";
      this.form.panelList = "";
      this.form.actionList = "";
      this.devListOpts = [];
      this.btnListOpts = [];
      this.panelListOpts = [];
      this.actionListOpts = [];
    },
    changeOfType (item) {
      let type = "";
      this.initialform();
      switch (item) {
        case 1:
          type = "41,42,43";
          break;
        case 2:
          type = "44";
          break;
        case 3:
          type = "45";
          break;
        case 4:
          type = "46";
          break;
        case 5:
          type = "47";
          break;
      }
      if (item === 0) {
        this.getScene();
      } else {
        this.getRHDevByRT(type);
      }
    },
    async getRHDevByRT (type) {
      const res = await api.IOT.GetRHDeviceByRT(this.roomID, type);
      this.devListOpts = [];
      if (res.Datas && res.Datas.length > 0) {
        res.Datas.forEach(ele => {
          this.devListOpts.push({
            label: ele.DeviceName,
            value: ele.DeviceSerialNo,
            data: ele
          });
        });
      }
    },
    async getScene () {
      const res = await api.IOT.GetRHSceneByCondition(this.roomID);
      this.sceneListOpts = [];
      if (res.Result) {
        if (res.Datas.length > 0) {
          if (res.Datas[0].Scenes.length > 0) {
            const secen = JSON.parse(res.Datas[0].Scenes);
            secen.forEach(ele => {
              this.sceneListOpts.push({
                label: ele.SceneName,
                value: ele.SceneType,
                data: ele
              });
            });
          }
        }
      }
    },
    selectDevice (val) {
      this.btnListOpts = [];
      this.actionListOpts = [];
      this.panelListOpts = [];
      let panle;
      let sub;
      let subaka;
      let fan;
      let motor;
      switch (val.data.DeviceType) {
        case 41:
        case 42:
        case 43:
          this.form.btnList = "";
          sub = JSON.parse(val.data.SubAKA);
          sub.forEach(ele => {
            this.btnListOpts.push({
              label: ele.name,
              value: ele.id
            });
          });
          panle = [
            { label: "关", value: 0 },
            { label: "开", value: 1 }
          ];
          this.actionListOpts = panle;
          break;
        case 45:
          subaka = JSON.parse(val.data.SubAKA);
          subaka.forEach(ele => {
            this.panelListOpts.push({
              label: ele.Name,
              value: ele.Code ? ele.Code : ele.ModelID,
              data: ele
            });
          });
          break;
        case 46:
          fan = [
            { label: "关", value: 0 },
            { label: "1档", value: 1 },
            { label: "2档", value: 2 },
            { label: "3档", value: 3 },
            { label: "4档", value: 4 },
            { label: "5档", value: 5 },
            { label: "6档", value: 6 },
            { label: "7档", value: 7 },
            { label: "8档", value: 8 },
            { label: "9档", value: 9 },
            { label: "10档", value: 10 }
          ];
          this.actionListOpts = fan;
          break;
        case 47:
          motor = [
            { label: "拉开", value: 0 },
            { label: "关闭", value: 1 },
            { label: "停止", value: 2 }
          ];
          this.actionListOpts = motor;
          break;
        default:
          break;
      }
    },
    async selectPanel (val) {
      this.form.actionList = "";
      this.actionListOpts = [];
      let actions;
      let res;
      switch (val.data.Type) {
        case 1:
          this.isCustom = true;
          actions = [
            { label: "开", value: 1 },
            { label: "关", value: 0 }
          ];
          this.actionListOpts = actions;
          break;
        case 2:
          this.isCustom = false;
          res = await api.CMDS.GetCMDByMID(val.data.ModelID);
          if (res.Result) {
            if (res.Datas != null && res.Datas.length > 0) {
              res.Datas.forEach(element => {
                this.actionListOpts.push({
                  label: element.CMDName,
                  value: element.CMDContent
                });
              });
            }
          }
          break;
      }
    },
    saveAction () {
      this.$refs["form"].validate(valid => {
        let actionModel;
        if (valid) {
          actionModel = {
            actionType: "", // 区分动作类型 0-设备 1-场景
            type: "", // 设备类型
            devName: "", // 设备名称
            serialID: "", // 设备序列号
            vpid: "", // 虚拟面板ID
            action: "", // 动作
            eq: "", // 通道
            eqName: "", // 通道名称
            w: "", // 白光度
            y: "", // 黄光度
            b0: "", // 空调温度
            b6: "", // 空调控制类型
            panelName: "", // 虚拟面板名称
            panelType: "", // 虚拟面板类型 1-空调 0自定义
            actionName: "", // 自定义名称
            index: "", // 动作顺序
            code: "", // 红外码/型号
            scene: "", // 情景类型
            sceneName: "", // 情景名称
            time: "" // 延迟时间
          };
          if (this.form.devType === 0) {
            actionModel.type = 0;
            actionModel.scene = this.form.sceneList.value;
            actionModel.sceneName = this.form.sceneList.label;
            actionModel.actionType = 1;
          } else {
            actionModel.actionType = 0;
            actionModel.type = this.form.devList.data.DeviceType;
            actionModel.devName = this.form.devList.label;
            actionModel.serialID = this.form.devList.data.DeviceSerialNo;
            actionModel.time = this.form.delayed * 1000;
            switch (this.form.devType) {
              case 1:
                actionModel.action = this.form.actionList.value;
                actionModel.eq = this.form.btnList.value;
                actionModel.eqName = this.form.btnList.label;
                break;
              case 2:
                actionModel.w = this.form.white;
                actionModel.y = this.form.yellow;
                break;
              case 3:
                actionModel.vpid = this.form.panelList.data.ID;
                actionModel.panelType = this.form.panelList.data.Type;
                actionModel.panelName = this.form.panelList.label;
                if (actionModel.panelType === 1) {
                  actionModel.action = this.form.actionList.value;
                  actionModel.b0 = this.form.temperature;
                  actionModel.b6 = this.form.patternList;
                  actionModel.code = this.form.panelList.value;
                } else {
                  actionModel.code = this.form.actionList.value;
                  actionModel.actionName = this.form.actionList.label;
                }
                break;
              case 4:
              case 5:
                actionModel.action = this.form.actionList.value;
                break;
            }
          }
          this.actions.push(actionModel);
          this.form.devType = "";
          this.isAdd = true;
        }
      });
    }
  }
};
</script>

<style lang="less">
.actionSet {
  height: 100%;
  color: #72767b;
  margin-top: 10px;
  > p {
    color: #72767b;
    margin: 0px 0px 20px 0;

    .el-button-group {
      float: right;
      margin-right: 15px;

      .add {
        color: #409eff;
        background: #ecf5ff;
        border-color: #b3d8ff;
      }
      .add:hover {
        color: #fff;
        background-color: #409eff;
        border-color: #409eff;
      }
      .add:active {
        background: #3a8ee6;
        border-color: #3a8ee6;
        color: #fff;
        outline: 0;
      }
      .update {
        color: #409eff;
        background: #ecf5ff;
        border-color: #b3d8ff;
        border-left-color: none;
        border-right-color: rgba(255, 255, 255, 0.5);
      }
      .update:hover {
        color: #fff;
        background-color: #409eff;
        border-color: #409eff;
      }
      .update:active {
        background: #3a8ee6;
        border-color: #3a8ee6;
        color: #fff;
        outline: 0;
      }
      .delete {
        color: #f56c6c;
        background: #fef0f0;
        border-color: #fbc4c4;
        border-left-color: rgba(255, 255, 255, 0.5);
      }
      .delete:hover {
        color: #fff;
        background-color: #f56c6c;
        border-color: #f56c6c;
      }
      .delete:active {
        background: #dd6161;
        border-color: #dd6161;
        color: #fff;
        outline: 0;
      }
    }
  }
  > .el-form {
    margin: 0px 15px;
  }
  .set {
    width: 94%;
    height: 90%;
    text-align: center;
    padding: 0px 10px;
    overflow: hidden;
    overflow-y: auto;
    .action-item {
      height: 45px;
      line-height: 45px;
      color: #409eff;
      background-color: #e8f3fe;
      text-align: center;
      margin: 10px 0px;
      span:nth-of-type(1) {
        float: left;
        margin-left: 10px;
      }
      span:last-child {
        float: right;
        > i {
          display: inline-block;
          width: 20px;
          height: 20px;
          border: 1px solid;
          margin-right: 10px;
          line-height: 20px;
        }
        > i:hover {
          color: #fff;
          background-color: #409eff;
          border-color: #409eff;
        }
        > i:active {
          background: #3a8ee6;
          border-color: #3a8ee6;
          color: #fff;
        }
      }
    }

    > span {
      font-size: 48px;
      line-height: 250px;
      color: #c7cdd3;
    }
  }
}
</style>