<template>
  <div class="student_question" v-loading="dataLoading">
    <el-row :gutter="20">
      <el-col :span="6" v-for="(item, index) in scorePreview" :key="index">
        <div class="question_item" @click="dialogVisible = true">
          <div class="question_title">{{formatDate(item['CreateTime'], 'hh:mm:ss')}}</div>
          <div class="question_info"> {{item['QContent']}}</div>
        </div>
      </el-col>
    </el-row>

    <el-dialog title="提问记录明细" :visible.sync="dialogVisible" :close-on-click-modal="false"
               :modal-append-to-body="false">
      <!-- 提问记录 -->
      <div class="chat" ref="chat">

        <div v-if="dataList && dataList.length>0" class="chat_content">
          <div v-for="(sub, k) in dataList" :key="k" class=" chat_item">
            <div class="chat_news">
              <div class="chat_news_name">
                {{sub['StudentName']}}
                <span>{{formatDate(sub['CreateTime'], 'hh:mm:ss')}}</span>
              </div>
              <div class="chat_news_cont">
                <span>{{sub['QContent'] }}</span>
              </div>
            </div>
          </div>
        </div>

      </div>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/scripts/api";
import store from "@/scripts/store";
import { FormatDate, getSession } from "@/scripts/utils";

export default {
  name: "StudentQuestion",
  data () {
    return {
      // course: JSON.parse(sessionStorage.getItem("course")),
      // courseID: sessionStorage.getItem("courseID"),
      courseRecordID: getSession("campus_CourseRecordID"),
      courseRecord: getSession("campus_CourseRecord"),
      dataLoading: false,
      dataList: [],
      dialogVisible: false
    };
  },
  created () {
    this.getStudentQuestion();
  },
  methods: {
    formatDate (dateValue, format) {
      return FormatDate(dateValue, format);
    },
    // 获取学生提问
    getStudentQuestion () {
      this.dataList = [];
      this.dataLoading = true;
      const studentInfo = store.GetStudentInfo();
      api.IQuestion.StudentDataList(
        this.courseRecordID,
        studentInfo ? studentInfo.StudentID : "",
        2
      ).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.dataList.push(item);
          });
        }
        this.dataLoading = false;
      }).catch(() => {
        this.dataLoading = false;
      });
    }
  },
  computed: {
    scorePreview () {
      return this.dataList.filter((r, k) => k < 4);
    }
  }
}
</script>

