<template>
  <el-dialog
    :title="`选择IP广播云端文件 - ${title}文件`"
    :visible.sync="visible"
    :modal-append-to-body="false"
    :close-on-click-modal="false"
    width="900px"
    custom-class="yun-dlg"
    center
    @open="OpenDlg"
  >
    <div class="con-wrap">
      <SubTable
        ref="table"
        :Subbutton="true"
        :Columns="tableColumns"
        :Datas.sync="tableDatas"
        :ShowCreate="false"
        :Loading="listLoading"
        :singleChoose="true"
        :selections.sync="selects"
        :showMaxeight="false"
        :ShowPage="true"
        :PageIndex.sync="PageIndex"
        :PageSize.sync="PageSize"
        :TotalCounts="TotalCounts"
      >
        <template slot="MaterialFileSize" slot-scope="scope">
          <span>{{scope.row.MaterialFileSize | RenderSize }}</span>
        </template>
        <template slot="MaterialCreateDate" slot-scope="scope">
          <span>{{scope.row['MaterialCreateDate']|FormatDateTime}}</span>
        </template>
      </SubTable>
    </div>
    <div slot="footer">
      <el-button-group>
        <el-button
          type="primary"
          icon="el-icon-check"
          size="mini"
          @click="cfmChoose"
          autofocus
          native-type="submit"
        >确 定</el-button>
        <el-button icon="el-icon-close" size="mini" @click="visible = false">取 消</el-button>
      </el-button-group>
    </div>
  </el-dialog>
</template>
<script>
import api from "@/scripts/api";
export default {
  props: {
    show: {
      type: Boolean,
      default: false,
    },
    type: {
      type: String,
      default: "video",
    },
  },
  components: { SubTable: () => import("@/components/SubTable") },
  data() {
    return {
      PageIndex: 1,
      TotalCounts: 0,
      tableDatas: [],
      selects: [],
      listLoading: false,
      typeSelected: "",
      tableColumns: [
        {
          Code: "MaterialName",
          Header: "名称",
        },
        {
          Code: "MaterialFileExt",
          Header: "格式",
          width: 100,
        },
        {
          Code: "MaterialFileSize",
          Header: "大小",
          Slot: true,
          width: 100,
        },
        {
          Code: "MaterialCreateDate",
          Header: "创建时间",
          Slot: true,
          width: 100,
        },
      ],
    };
  },
  computed: {
    visible: {
      get() {
        return this.show;
      },
      set(val) {
        this.$emit("update:show", val);
      },
    },
    title() {
      const titleMap = {
        video: "视频",
        audio: "音频",
        doc: "文档",
      };
      return titleMap[this.type];
    },
  },
  methods: {
    OpenDlg() {
      this.searchData();
    },
    searchData() {
      this.tableDatas = [];
      this.listLoading = true;
      api.Material.GetIPBMaterials(
        this.type,
        "",
        this.PageIndex,
        this.PageSize,
        "",
        ""
      )
        .then((res) => {
          console.log(res && res.Result);
          if (res) {
            this.tableDatas = res.Datas;
            this.TotalCounts = res.RowsCount;
            this.TotalCounts = res.RowsCount;
            this.PageIndex = res.PageIndex;
          }
          this.listLoading = false;
        })
        .catch(() => {
          this.listLoading = false;
        });
    },
    cfmChoose() {
      this.visible = false;
      this.$emit("OnChooseFile", this.selects);
    },
  },
};
</script>
<style lang="less">
.yun-dlg {
  .con-wrap {
    border: 1px solid #ddd;
  }
  .el-table .el-table__header {
    font-size: 14px;
    th {
      color: #333;
      font-weight: 400;
    }
  }
  .el-table__body {
    color: #999;
    font-size: 12px;
  }
  .el-dialog__body {
    height: 500px;
  }

  .el-table {
    margin-top: 0;
    border: 0;
  }
  #comTable {
    margin-top: 0;
    border: 0;
  }
  #comTable .el-table__header thead tr th {
    padding: 8px 5px;
    color: #333;
    background-color: #fff;
  }
}
</style>
