<template>
  <div class="areaSel">
    <el-divider content-position="left">
      <el-switch
        v-model="AllRoom"
        active-text="全部教室"
        inactive-text="部分教室"
        @change="SiwtchChange"
      ></el-switch>
    </el-divider>
    <el-tabs
      v-model="activeName"
      type="border-card"
      v-show="!AllRoom"
      @tab-click="Handle"
    >
      <el-tab-pane name="areaTree" label="区域选择">
        <el-scrollbar class="scrollbar-Tree">
          <el-tree
            :data="AreaDatas"
            :props="props"
            node-key="ID"
            show-checkbox
            ref="areaTree"
            @check="SyncCheck('areaTree')"
          ></el-tree>
        </el-scrollbar>
      </el-tab-pane>
      <el-tab-pane name="deptTree" label="部门选择">
        <el-scrollbar class="scrollbar-Tree">
          <el-tree
            :data="DeptDatas"
            :props="props"
            node-key="ID"
            show-checkbox
            ref="deptTree"
            @check="SyncCheck('deptTree')"
          ></el-tree>
        </el-scrollbar>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import api from "@/scripts/api";
export default {
  props: {
    values: {}, // 清空时值必须为[];
    mcode: {
      type: String,
      default: ""
    }
  },
  data () {
    return {
      activeName: "areaTree",
      props: {
        value: "ID",
        id: "ID",
        label: "Name",
        children: "options"
      },
      AllRoom: true,
      AreaDatas: [],
      DeptDatas: [],
      Selecteds: [],
      rid: false
    };
  },
  watch: {
    values (newVal, oldVal) {
      if (newVal instanceof Array && newVal[0] !== true) {
        this.AllRoom = false;
        if (!this.rid) {
          console.log("更新教室选择");
          if (newVal.length === 0) {
            this.AllRoom = true;
            this.$emit("update:values", true);
          }
          this.Selecteds = newVal;
          this.$refs[this.activeName].setCheckedKeys(newVal);
        }
      }
      if (newVal === true) {
        this.AllRoom = true;
      }
      this.rid = false;
    },
    AllRoom (val) {
      this.$emit('Switch', val)
    }
  },
  mounted () {
    this.LoadData();
  },
  methods: {
    /**
     * 加载数据
     */
    async LoadData () {
      const areas = [];
      const depts = [];
      const resC = await api.Campuses.GetAllDatas(); // 加载校区
      const resR = await api.ClsRoom.GetDatasBySchool(); // 加载教室

      const resD = await api.Dept.GetAllDepts(); // 加载部门

      if (resC) {
        await resC.Datas.forEach(element => {
          areas.push({
            Level: 1,
            ID: element.CampusesID,
            Name: element.CampusesName,
            options: []
          });
        });

        const resB = await api.Building.GetAllDatasByCampuses(); // 加载建筑物
        if (resB) {
          await resB.Datas.forEach(element => {
            const findC = areas.find(c => c.ID === element.CampusesID);
            if (findC) {
              findC.options.push({
                Level: 2,
                ID: element.BuildingID,
                Name: element.BuildingName,
                options: []
              });
            }
          });
        }
      }

      if (resD) {
        await resD.Datas.forEach(element => {
          depts.push({
            Level: 1,
            ID: element.DeptID,
            Name: element.DeptName,
            options: []
          });
        });

        depts.push({
          Level: 1,
          ID: "",
          Name: "无所属教室",
          options: []
        });
      }

      if (resR) {
        await resR.Datas.forEach(async element => {
          const findC = areas.find(c => c.ID === element.CampusesID);

          if (findC) {
            const findB = findC.options.find(b => b.ID === element.BuildingID);

            if (findB) {
              findB.options.push({
                Level: 3,
                ID: element.ClsRoomID,
                Name: element.ClsRoomName,
                options: null
              });
            }
          }

          const findD = depts.find(c => c.ID === element.DeptID);
          if (findD) {
            findD.options.push({
              Level: 2,
              ID: element.ClsRoomID,
              Name: element.ClsRoomName,
              options: null
            });
          }
        });
      }

      this.RemoveEmpty(areas, 3);
      this.RemoveEmpty(depts, 2);
      this.AreaDatas = [];
      this.DeptDatas = [];
      for (var o of areas) {
        if (o.options.length > 0) {
          this.AreaDatas.push(o);
        }
      }
      for (var d of depts) {
        if (d.options.length > 0) {
          this.DeptDatas.push(d);
        }
      }
    },
    /**
     * 移除空项
     */
    async RemoveEmpty (options, level) {
      if (options) {
        for (let i = options.length - 1; i >= 0; i--) {
          if (options[i].Level < level) {
            if (!(options[i].options && options[i].options.length > 0)) {
              options.splice(i, 1);
            } else {
              this.RemoveEmpty(options[i].options, level);
            }
          }
        }
      }
    },
    /**
     * 同步两棵树
     */
    SiwtchChange (val) {
      if (val) this.$emit("update:values", true);
    },
    SyncCheck (s) {
      const keyA = this.$refs.areaTree.getCheckedKeys(true);
      const keyB = this.$refs.deptTree.getCheckedKeys(true);
      const obja = {
        areaTree: keyA,
        deptTree: keyB
      };
      const objb = {
        areaTree: keyB,
        deptTree: keyA
      };
      const arr = Array.from(new Set(keyA.concat(keyB)));
      const diff = this.Selecteds.filter(val => obja[s].indexOf(val) === -1);
      const del = [];
      diff.forEach(item => {
        const index = objb[s].indexOf(item);
        if (index > -1) {
          del.push(item);
        }
      });
      del.forEach(item => {
        const index = arr.indexOf(item);
        if (index > -1) {
          arr.splice(index, 1);
        }
      });
      this.rid = true;
      this.Selecteds = obja[s];
      this.$emit("update:values", arr);
    },
    Handle () {
      if (this.values instanceof Array) {
        this.$refs[this.activeName].setCheckedKeys(this.values);
      }
    }
  }
};
</script>

<style lang="less">
.areaSel {
  .el-divider {
    margin-top: 10px;
  }

  .el-tabs {
    .el-tabs__content {
      .el-tab-pane {
        .scrollbar-Tree {
          height: 200px;
        }
      }
    }
  }
}
</style>
