<template>
  <div id="lesson-session">
    <el-dialog
      title="编辑课节"
      :visible.sync="show"
      center
      :close-on-click-modal="false"
      :modal-append-to-body="false"
      append-to-body
      @close="close"
      custom-class="session-my"
    >
      <Table
        ModuleName="节次"
        KeyFileld="LessonID"
        EstateFiled="LessonEstate"
        :LessonEstate.sync="Estate"
        :Columns="Columns"
        :BaseForm.sync="BaseForm"
        :BaseFormRule.sync="Rules"
        :Datas.sync="Datas"
        Mclass="se-table"
        @UpdateEstate="UpdateEstate"
        @LoadData="LoadData"
        @Delete="Delete"
        @EditRowHandle="EditRowHandle"
        @ConfirmBaseForm="ConfirmForm"
        @CreateHandle="CreateHandle"
      >
        <template slot="BaseForm">
          <el-row :gutter="5" v-for="(item, index) in formList" :key="index">
            <el-col :span="24">
              <el-form-item :label="item.label + ': '" :prop="item.prop">
                <el-input type="textarea" :rows="5" v-if="item.type === 'desc'" v-model="BaseForm[item.prop]"></el-input>
                <el-input-number v-model="BaseForm[item.prop]" v-if="item.type == 'step'" :min="0"></el-input-number>
                <el-time-picker v-model="BaseForm[item.prop]" v-if="item.type == 'time'" value-format="HH:mm:ss"></el-time-picker>
              </el-form-item>
            </el-col>
          </el-row>
        </template>
      </Table>
    </el-dialog>
  </div>
</template>
<script>
import api from "@/scripts/api"
export default {
  components: {
    Table: () => import("@/components/SubTable")
  },
  props: {
    id: {
      type: String,
      default: ""
    },
    visible: {
      type: Boolean,
      default: false
    }
  },
  data() {
    // 验证课节名称
    const ValidateSession = async (rule, name, callback) => {
      const res = await api.Lesson.ValidateSession(this.id, name, this.BaseForm.LessonID)
      if (res.Result) {
        callback()
      } else {
        callback(new Error("节次已存在"))
      }
    }
    // 验证时间
    const ValidateTime1 = async (rule, name, callback) => {
      if (!name) {
        callback(new Error("请填写上课时间"))
      }
      const res = await api.Lesson.ValidateTime(this.id, name, this.BaseForm.LessonID)
      if (res.Result) {
        callback()
      } else {
        callback(new Error("上课时间已存在"))
      }
    }
    // 验证时间
    const ValidateTime2 = (rule, name, callback) => {
      if (!name) {
        callback(new Error("请填写下课时间"))
      }
      const start = parseInt(this.BaseForm.LessonClsTime.replace(/:/g, ""))
      const end = parseInt(name.replace(/:/g, ""))
      if (start > end) {
        callback(new Error("下课时间不能早于上课时间"))
      } else {
        callback()
      }
    }
    return {
      show: false,
      Columns: [
        {
          Code: "LessonSession",
          Header: "节次",
          Fixed: "left"
        },
        {
          Code: "LessonClsTime",
          Header: "上课时间"
        },
        {
          Code: "LessonOutTime",
          Header: "下课时间"
        },
        {
          Code: "LessonEstate",
          Header: "状态",
          Filters: [
            { text: "正常", value: 1 },
            { text: "停用", value: 0 }
          ],
          IsEstate: true
        },
        {
          Code: "oprate",
          Header: "操作",
          Slot: true,
          ShowBaseActionBtn: true,
          Fixed: "right",
          Align: "center",
          Width: "250"
        }
      ],
      Datas: [],
      Estate: "",
      formList: [
        {
          label: "节次",
          prop: "LessonSession",
          type: "step"
        },
        {
          label: "上课时间",
          prop: "LessonClsTime",
          type: "time"
        },
        {
          label: "下课时间",
          prop: "LessonOutTime",
          type: "time"
        },
        {
          label: "提前解锁时间",
          prop: "Unlocking",
          type: "step"
        },
        {
          label: "延迟关机时间",
          prop: "TurnOff",
          type: "step"
        },
        {
          label: "备注",
          prop: "LessonDescription",
          type: "desc"
        }
      ],
      Rules: {
        LessonSession: [
          {
            required: true,
            trigger: "blur",
            validator: ValidateSession
          }
        ],
        LessonClsTime: [
          {
            required: true,
            trigger: "change",
            validator: ValidateTime1
          }
        ],
        LessonOutTime: [
          {
            required: true,
            trigger: "change",
            validator: ValidateTime2
          }
        ]
      },
      BaseForm: {
        LessonID: "",
        LessonSession: "1",
        LessonGrpID: "",
        LessonClsTime: "",
        LessonOutTime: "",
        LessonDescription: "",
        Unlocking: "10",
        TurnOff: "10"
      }
    }
  },
  watch: {
    visible(val) {
      this.show = val
      this.BaseForm.LessonGrpID = this.id
      if (val) this.LoadData()
    }
  },
  methods: {
    close() {
      this.$emit("close")
    },
    async LoadData() {
      this.Loading = true
      const res = await api.Lesson.GetAllDatas(this.id)
      if (res && res.Result) {
        this.Datas = res.Datas
      } else {
        this.Datas = []
      }
      this.Loading = false
    },
    /**
     * 删除
     */
    async Delete(ids) {
      await api.Lesson.DeleteSession(ids)
      this.LoadData()
    },
    /**
     * 停用/启用
     */
    async UpdateEstate(ids, estate) {
      await api.Lesson.UpdateSessionEstate(ids, estate)
      this.LoadData()
    },
    /**
     * 编辑行数据
     */
    async EditRowHandle(row) {
      const res = await api.Lesson.GetSession(row.LessonID)
      const data = res.Data
      if (res.Data) {
        for (var keys in this.BaseForm) {
          this.BaseForm[keys] = data[keys]
        }
      }
    },
    /**
     * 提交表单
     */
    async ConfirmForm(callback) {
      const data = []
      for (var b in this.BaseForm) {
        data.push(this.BaseForm[b])
      }
      const res = await api.Lesson.Save(...data)
      if (res) {
        callback()
      } else {
        this.$message({
          message: res.Info,
          type: "error",
          showClose: true,
          duration: 1000
        })
      }
    },
    CreateHandle() {
      this.BaseForm.LessonID = ""
    }
  }
}
</script>
<style lang="less">
#lesson-session {
  .el-dialog {
    min-width: 1000px;
    width: 50%;
  }
}
.session-my {
  .el-dialog__header {
    border-bottom: 1px solid #ddd;
  }
}
.se-table {
  width: 500px;
}
</style>
