<template>
  <div class="drag-pane-wrap clearfix" id="paneWrap">
    <div
      class="drag-pane drag-panel"
      id="paneL"
      :style="{ width: paneLW + 'px' }"
    >
      <slot name="paneL" />
    </div>
    <div
      class="drag-pane-resizer"
      id="resizer"
      :style="{ left: paneLW + 'px' }"
    ></div>
    <div
      class="drag-pane drag-paner"
      :class="{ 's-wrap': isSWrap }"
      id="paneR"
      :style="{ width: 'calc(100% - ' + paneLW + 'px)' }"
    >
      <slot name="paneR" />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    minWidth: {
      type: Number,
      default: 180
    },
    maxWidth: {
      type: Number,
      default: 240
    },
    collapse: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      paneLW: this.minWidth,
      isSWrap: false
    };
  },
  watch: {
    collapse(val) {
      if (val) {
        this.paneLW = 0;
        this.isSWrap = false;
      } else {
        this.paneLW = this.minWidth;
      }
    }
  },
  mounted() {
    this.dragControllerDiv();
  },
  methods: {
    dragControllerDiv: function() {
      const resizer = document.getElementById("resizer");
      const paneWrap = document.getElementById("paneWrap");
      // 鼠标按下事件
      resizer.onmousedown = e => {
        const startX = e.clientX;
        resizer.left = resizer.offsetLeft;
        // 鼠标拖动事件
        document.onmousemove = e => {
          const endX = e.clientX;
          let moveLen = resizer.left + (endX - startX); // （endx-startx）=移动的距离。resizer.left+移动的距离=左边区域最后的宽度

          if (moveLen < this.minWidth) moveLen = this.minWidth; // 左边区域的最小宽度
          if (moveLen > this.maxWidth) moveLen = this.maxWidth; // 左边区域的最大宽度

          this.paneLW = moveLen;
          const paneRW = parseInt(paneWrap.clientWidth) - moveLen;
          if (paneRW < 1200) {
            this.isSWrap = true;
          } else {
            this.isSWrap = false;
          }
        };
        // 鼠标松开事件
        document.onmouseup = function(e) {
          document.onmousemove = null;
          document.onmouseup = null;
          resizer.releaseCapture && resizer.releaseCapture(); // 当你不在需要继续获得鼠标消息就要应该调用ReleaseCapture()释放掉
        };
        resizer.setCapture && resizer.setCapture(); // 该函数在属于当前线程的指定窗口里设置鼠标捕获
        return false;
      };
    }
  }
};
</script>

<style lang="less" scoped>
.clearfix::after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}
.drag-pane-wrap {
  height: 100%;
  position: relative;
}
.drag-pane.drag-panel {
  position: absolute;
  left: 0;
  height: 100%;
}
.drag-pane-resizer {
  box-sizing: border-box;
  position: absolute;
  opacity: 0.2;
  z-index: 1;
  background-clip: padding-box;
}
.drag-pane-resizer {
  width: 11px;
  height: 100%;
  margin-left: -5px;
  border-left: 5px solid hsla(0, 0%, 100%, 0);
  border-right: 5px solid hsla(0, 0%, 100%, 0);
  cursor: col-resize;
}
.drag-pane.drag-paner {
  position: absolute;
  right: 0;
  height: 100%;
}
</style>
<style lang="less">
// 调整卡片布局
.s-wrap {
  .mc {
    .rooms {
      grid-template-columns: repeat(5, 1fr);
    }
    .rooms.mini {
      grid-template-columns: repeat(5, 1fr);
    }
  }
  // 综合管控countbar
  .mc > .el-card .el-card__body {
    grid-template-columns: auto !important;
  }
  .el-card.countbar .el-card__body .el-button-group {
    width: 582px;
    justify-self: end;
  }
}
</style>
